<template>
    <div>
        <b-container fluid class="pr-md-3 py-5 pr-5">
            <b-container fluid class="py-5">
                <b-row>
                    <b-col md="4" class="text-left">
                        <div class="position-sticky sticky-top" style="top: 100px; z-index: 10">
                            <div class="contact-information p-4">
                                <h3 class="nunito pl-3 font-weight-bold">Contact</h3>
                                <div class="line-white"></div>
                                <p class="pt-4 nunito text-sm">Fill up the form and our Team will get back to you within 24 hours</p>
                                <div class="contact-list">
                                    <div class="py-3" id="email">
                                        <b-row>
                                            <b-col cols="2" class="text-center">
                                                <icon icon="envelope"/>
                                            </b-col>
                                            <b-col cols="10" class="text-left">
                                                <span class="nunito text-sm">sidescriptdeveloper@gmail.com</span>
                                            </b-col>
                                        </b-row>
                                    </div>
                                    <div class="py-3" id="instagram">
                                        <b-row>
                                            <b-col cols="2" class="text-center">
                                                <icon :icon="['fab', 'instagram']"/>
                                            </b-col>
                                            <b-col cols="10" class="text-left">
                                                <span class="nunito text-sm">sidescript_dev</span>
                                            </b-col>
                                        </b-row>
                                    </div>
                                    <div class="py-3" id="facebook">
                                        <b-row>
                                            <b-col cols="2" class="text-center">
                                                <icon icon="map-marker-alt"/>
                                            </b-col>
                                            <b-col cols="10" class="text-left">
                                                <span class="nunito text-sm">La Royba Regency No 19, Purwosari, Pasuruan</span>
                                            </b-col>
                                        </b-row>
                                    </div>
                                </div>
                                <img src="@/assets/img/contact.png" alt="decoration-contact" class="decoration">
                            </div>
                        </div>
                    </b-col>
                    <b-col md="8" class="px-md-5 px-0 text-left">
                        <b-row class="py-4 pl-3  pr-5">
                            <b-col sm="6">
                                <b-form-group class="py-2" id="firstname-group">
                                    <label class="nunito" for="firstname">First Name</label>
                                    <b-form-input id="firstname" :state="validateState('firstname')" type="text" v-model="$v.input.firstname.$model" aria-describedby="firstname-feedback"></b-form-input>
                                    <b-form-invalid-feedback id="firstname-feedback">Required & Min 4 Character</b-form-invalid-feedback>
                                </b-form-group>
                            </b-col>
                            <b-col sm="6">
                                <b-form-group class="py-2" id="lastname-group">
                                    <label class="nunito" for="lastname">Last Name</label>
                                    <b-form-input id="lastname" :state="validateState('lastname')" type="text" v-model="$v.input.lastname.$model" aria-describedby="lastname-feedback"></b-form-input>
                                    <b-form-invalid-feedback id="lastname-feedback">Required & Min 4 Character</b-form-invalid-feedback>
                                </b-form-group>
                            </b-col>
                            <b-col sm="6">
                                <b-form-group class="py-2" id="email-group">
                                    <label class="nunito" for="email">Mail</label>
                                    <b-form-input id="email" :state="validateState('email')" type="email" v-model="$v.input.email.$model" aria-describedby="email-feedback"></b-form-input>
                                    <b-form-invalid-feedback id="email-feedback">Required & Must email formatter</b-form-invalid-feedback>
                                </b-form-group>
                            </b-col>
                            <b-col sm="6">
                                <b-form-group class="py-2" id="phone-group">
                                    <label class="nunito" for="phone">Phone</label>
                                    <b-form-input id="phone" :state="validateState('phone')" type="tel" v-model="$v.input.phone.$model" aria-describedby="phone-feedback"></b-form-input>
                                    <b-form-invalid-feedback id="phone-feedback">Required & Min 10 Character</b-form-invalid-feedback>
                                </b-form-group>
                            </b-col>
                            <b-col sm="12">
                                <h5 class="nunito">What the of website do you need ?</h5>
                                <b-row class="py-3">
                                    <b-col md="3" sm="6" class="py-sm-0 py-3">
                                        <div class="check-type-contact" @click="input.type = 'web|mobile'" :class="{'active': input.type == 'web|mobile'}">
                                            <div class="check">
                                                <icon icon="check"></icon>
                                            </div>
                                        </div>
                                        <p class="mb-0 text-sm label-check-type">Web Dev | Mobil Dev</p>
                                    </b-col>
                                    <b-col md="3" sm="6" class="py-sm-0 py-3">
                                        <div class="check-type-contact" @click="input.type = 'design|illust'" :class="{'active': input.type == 'design|illust'}">
                                            <div class="check">
                                                <icon icon="check"></icon>
                                            </div>
                                        </div>
                                        <p class="mb-0 text-sm label-check-type">Design | Illustration</p>
                                    </b-col>
                                    <b-col md="3" sm="6" class="py-sm-0 py-3">
                                        <div class="check-type-contact" @click="input.type = 'data|ai'" :class="{'active': input.type == 'data|ai'}">
                                            <div class="check">
                                                <icon icon="check"></icon>
                                            </div>
                                        </div>
                                        <p class="mb-0 text-sm label-check-type">Data Science | AI</p>
                                    </b-col>
                                    <b-col md="3" sm="6" class="py-sm-0 py-3">
                                        <div class="check-type-contact" @click="input.type = 'other'" :class="{'active': input.type == 'other'}">
                                            <div class="check">
                                                <icon icon="check"></icon>
                                            </div>
                                        </div>
                                        <p class="mb-0 text-sm label-check-type">Other</p>
                                    </b-col>
                                </b-row>
                                <b-form-group class="py-2" id="message-group">
                                    <label class="nunito" for="message">Message</label>
                                    <b-form-textarea id="message" :state="validateState('message')" rows="5" v-model="$v.input.message.$model" aria-describedby="message-feedback"></b-form-textarea>
                                    <b-form-invalid-feedback id="message-feedback">Required</b-form-invalid-feedback>
                                </b-form-group>
                                <div class="text-right">
                                    <button @click="sendMessage()" :class="{'btn-disabled': input.type == null}" :disabled="input.type == null" class="border-0 m-button-primary d-inline-block">Send Message</button>
                                </div>
                            </b-col>
                        </b-row>
                    </b-col>
                </b-row>
            </b-container>
        </b-container>
    </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, minLength, email } from 'vuelidate/lib/validators'
export default {
    mixins: [validationMixin],
    data() {
        return {
            input: {
                firstname: '',
                lastname: '',
                email: '',
                phone: '',
                type: null,
                message: '',
            }
        }
    },
    validations: {
        input: {
            firstname: { required, minLength: minLength(4) },
            lastname: { required, minLength: minLength(4) },
            email: { required, email },
            phone: { required, minLength: minLength(11) },
            message: { required },
        }
    },
    methods: {
        validateState(name) {
            const { $dirty, $error } = this.$v.input[name]
            return $dirty ? !$error : null
        },
        sendMessage() {
            this.$v.input.$touch()
            if(this.$v.input.$anyError) {
                return
            } else {
                alert('Hei')
            }
        }
    }
}
</script>